import { AnimatePresence, motion } from "framer-motion";
import { useState } from "react";
import styled from "styled-components";

const Wrapper = styled(motion.div)`
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(135deg, rgb(0, 225, 255), rgb(71, 0, 255));
  flex-direction: column;
`;

const Box = styled(motion.div)`
  width: 400px;
  height: 200px;
  background-color: rgba(255, 255, 255, 1);
  border-radius: 40px;
  position: absolute;
  top: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 28px;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.1), 0 10px 20px rgba(0, 0, 0, 0.06);
`;

const Text = styled.div`
  margin: 20px 0px;
  font-size: 20px;
  color: white;
  text-align: center;
  line-height: 2;
`;

const box = {
  // custom boolean  isBack 
  entry: (isBack: boolean) => ({
    x: isBack ? -500 : 500,
    opacity: 0,
    scale: 0,
  }),
  center: {
    x: 0,
    opacity: 1,
    scale: 1,
    transition: {
      duration: 0.4,
    },
  },
  exit: (isBack: boolean) => ({
    x: isBack ? 500 : -500,
    opacity: 0,
    scale: 0,
    transition: { duration: 0.4 },
  }),
};

function Slider2() {
  const [visible, setVisible] = useState(1);
  //    
  const [back, setBack] = useState(false);
  const nextPlease = () => {
    setBack(false);
    setVisible((prev) => (prev === 10 ? 10 : prev + 1));
  };
  //          next   
  const prevPlease = () => {
    setBack(true);
    setVisible((prev) => (prev === 1 ? 1 : prev - 1));
  };

  return (
    <Wrapper>
      <AnimatePresence
        // exit       prop
        // exitBeforeEnter
        custom={back}
      >
        {/*  key    element  .( )       */}
        <Box
          // custom boolean       
          custom={back}
          variants={box}
          initial="entry"
          animate="center"
          exit="exit"
          key={visible}
        >
          {visible}
        </Box>
      </AnimatePresence>
      <button onClick={prevPlease}>prev</button>
      <button onClick={nextPlease}>next</button>
      <Text>
        React key     element   re-render .
        <br />
           exit     , 
           
        <br />
          key  number  
        <br />
           next   entry exit  
        .  custom prop  .
        <br />
        custom prop  custom  variants     
        
        <br />
          entry exit    ,  
           .
        <br />
        AnimatePresence exitBeforeEnter prop  . exit   exit
          element 
      </Text>
    </Wrapper>
  );
}

export default Slider2;
